// constants
const ID = 'id';
const NAME = 'name,displayName,displayShortName';
const DIMENSION_ITEM = "dimensionItem~rename(".concat(ID, ")");
const LEGEND_SET = "".concat(ID, ",").concat(NAME);
const USER = "".concat(NAME, ",userCredentials[username]");
const ITEMS = "".concat(DIMENSION_ITEM, ",").concat(NAME, ",dimensionItemType");
const AXIS = "dimension,filter,legendSet[".concat(LEGEND_SET, "],items[").concat(ITEMS, "]");
const INTERPRETATIONS = 'id,created'; // nested fields map

export const nestedFields = {
  columns: AXIS,
  rows: AXIS,
  filters: AXIS,
  user: USER,
  interpretations: INTERPRETATIONS,
  legendSet: LEGEND_SET
};
export const extendFields = field => "".concat(field).concat(nestedFields[field] ? "[".concat(nestedFields[field], "]") : '');